import React from 'react'

import HomeIcons from './home-icons'
import ContabilidadImage from '../../../images/iconos/dashboard/calculator.svg'

function Contabilidad(props){
    return(
        <HomeIcons imageUrl={ContabilidadImage} title="Contabilidad" width="70" />
    )
}

export default Contabilidad



