import React from 'react'

import HomeIcons from './home-icons'
import ComprasImage from '../../../images/iconos/dashboard/cart.svg'

function Compras(props){
    return(
        <HomeIcons imageUrl={ComprasImage} title="Compras" width="75" />
    )
}

export default Compras



