import React from 'react'

import HomeIcons from './home-icons'
import ProveedoresImage from '../../../images/iconos/dashboard/truck.svg'

function Proveedores(props){
    return(
        <HomeIcons imageUrl={ProveedoresImage} title="Proveedores" width="75" />
    )
}

export default Proveedores



